import React, {useEffect, useState} from "react";
import profile from "../../assets/profileicon2.png";
import assured from "../../assets/verified.png";
import "./ProfileBio.css";
import {useSelector} from "react-redux";
import starColor from "../../assets/Color-star.png";
import starDull from "../../assets/dull-star.png";
import {setAlert} from "../../actions/alert";
import {useDispatch} from "react-redux";
import {useNavigate} from "react-router-dom";
import EditProfile from "../EditProfile/EditProfile";
import {requestAdmin} from "../../actions/myDetails";
export default function ProfileBio({user}) {
  const current = useSelector((state) => state.myDetailsReducer);
  const [projectGiven, setProjectGiven] = useState([]);
  const [selectedProject, setSelectedProject] = useState({});
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const show_modal = () => {
    const modal = document.getElementById("toggle_model_button");
    modal.click();
  };

  const filterData = (projects) => {
    const data = projects.filter((p) => p.project_status == "created");

    setProjectGiven([...data]);
  };
  useEffect(() => {
    if (current && current?.data) {
      filterData(current.data.projects_given);
    }
  }, [current]);
  const handleSelect = (e) => {
    const id = e.target.value;
    const data = current?.data?.projects_given.filter((p) => p._id == id);

    if (data)
      setSelectedProject({...data[0]})

  }
  useEffect(() => {
    if (selectedProject._id != null) {
      dispatch(setAlert(`Selected Project-${selectedProject.title}`, "info", 1500))
    }
  }, [selectedProject])
  const handleClick = () => {

    const model = document.getElementById("toggle_model_button");
    model.click();
  }

  const handleAssign = (e) => {
    e.preventDefault();
    setAlert("Requesting Admin", "info", 3000);
    dispatch(requestAdmin({d_id: user._id, p_id: selectedProject._id}));
  };

  return (
    <div className="student-card ">
      {/* model - edit */}
      <div
        className="modal fade "
        id="toggle_model"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
      >
        <div className="modal-dialog modal-dialog-centered modal-xl">
          <div className="modal-content text-center ">
            <EditProfile handleClick={() => handleClick()} />
            <input
              type="button"
              id="toggle_model_button"
              hidden
              data-bs-toggle="modal"
              data-bs-target="#toggle_model"
            />
          </div>
        </div>
      </div>

      {/* model - request */}

      <div
        className="modal fade "
        data-bs-backdrop="static"
        id="toggle_model_request"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
      >
        <div className="modal-dialog modal-dialog-centered modal-lg">
          <div className="modal-content text-center">
            {selectedProject._id != null ? (
              <div>
                <div className="fs-4 m-5">
                  You are assigning <b>{selectedProject?.title}</b> project to{" "}
                  {user?.first_name}-{user?.last_name}
                </div>
                <div className="d-flex justify-content-around">
                  <button
                    class="btn btn-danger my-3"
                    data-bs-dismiss="modal"
                    aria-label="Close"
                  >
                    Cancel
                  </button>
                  <button
                    className="btn btn-md my-3 project-add-submit-btn text-light"
                    data-bs-dismiss="modal"
                    aria-label="Close"
                    onClick={handleAssign}
                  >
                    Assign
                  </button>
                </div>
              </div>
            ) : (
              <div>
                <div className="fs-4 m-5">
                  Please, Select the project to assign...
                </div>
                <button
                  className="btn btn-md m-3 px-4 project-add-submit-btn"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                >
                  OK
                </button>
              </div>
            )}
            <input
              type="button"
              id="toggle_model_button"
              hidden
              data-bs-toggle="modal"
              data-bs-target="#toggle_model_request"
            />
          </div>
        </div>
      </div>

      <div className="profile">
        <div className="profile-head">
          <div className="prof">
            <div className="profile-img me-4">
              <img src={profile} alt="" />
            </div>
            <div className="profile-detail">
              <div className="student-name">
                <h2>
                  {user?.first_name}-{user?.last_name}
                  ({user?.rollno})
                </h2>
              </div>

              <div className="student-role">
                <h3>{user?.domain[0]}</h3>
              </div>
            </div>
          </div>
          {current?.data?.admin_verify &&
            <div className="profile-dept">

              <div className="options d-flex align-items-center">
                <div className="student-assured">
                  <img src={assured} alt="" />
                </div>
                {current?.data?._id == user?._id && (
                  <div className="edit " onClick={show_modal}>
                    <i class="fa-solid fa-pencil fs-5"></i>
                    <input
                      type="button"
                      hidden
                      id="toggle_model_button"
                      data-bs-toggle="modal"
                      data-bs-target="#toggle_model"
                    />
                  </div>
                )}
              </div>
              <div className="rating">
                {(function () {
                  var rate = [];
                  for (let i = 0; i < user?.rating; i++) {
                    rate.push(<img key={i} src={starColor} alt="star" height="30px" />);
                  }
                  for (let j = 0; j < 5 - user?.rating; j++) {
                    rate.push(<img key={j} src={starDull} alt="star" height="25px" />);
                  }
                  return <div key={rate.length}>{rate}</div>;
                })()}
              </div>
            </div>
          }
        </div>
        {
          user?.admin_verify &&
        <div className="student-dept d-flex flex-wrap justify-content-between">
          <h4 className="mb-2">
            <i class="fa-solid fa-location-dot px-3" ></i>
            {user?.department}
          </h4>
          {projectGiven.length != 0 && current?.data?._id != user?._id && (
            <div className="d-flex">
              <select
                className="form-select mx-3"
                onChange={(e) => handleSelect(e)}
              >
                <option selected disabled hidden>
                  Project
                </option>
                {projectGiven?.map((p) => (
                  <option value={p._id} key={p._id}>
                    {p.title}
                  </option>
                ))}
              </select>

              <button
                class="btn"
                style={{backgroundColor: "#16226D", color: "white"}}
                data-bs-toggle="modal"
                data-bs-target="#toggle_model_request"
              >
                Request
              </button>
            </div>
          )}
        </div>
        }
        {user?.user_type == 'freelancer' && user?.admin_verify == false ? <p className="text-start  text-success fs-5 text-bold">Waiting for Admin Approval *</p> : " "}
        <div className="student-about">
          <h2 className="my-3">About </h2>
          <div> {user?.description} </div>
        </div>
        <div className="d-flex my-3 mt-4">
          <div className="me-3">
            <a href={user?.linkedin} className="text-dark" target="_blank">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="currentColor"
                class="bi bi-linkedin"
                viewBox="0 0 16 16"
                color="#0a65c0"
              >
                <path d="M0 1.146C0 .513.526 0 1.175 0h13.65C15.474 0 16 .513 16 1.146v13.708c0 .633-.526 1.146-1.175 1.146H1.175C.526 16 0 15.487 0 14.854V1.146zm4.943 12.248V6.169H2.542v7.225h2.401zm-1.2-8.212c.837 0 1.358-.554 1.358-1.248-.015-.709-.52-1.248-1.342-1.248-.822 0-1.359.54-1.359 1.248 0 .694.521 1.248 1.327 1.248h.016zm4.908 8.212V9.359c0-.216.016-.432.08-.586.173-.431.568-.878 1.232-.878.869 0 1.216.662 1.216 1.634v3.865h2.401V9.25c0-2.22-1.184-3.252-2.764-3.252-1.274 0-1.845.7-2.165 1.193v.025h-.016a5.54 5.54 0 0 1 .016-.025V6.169h-2.4c.03.678 0 7.225 0 7.225h2.4z" />
              </svg>
            </a>
          </div>
          <div className="mx-3">
            <a href={user?.github} className="text-dark" target="_blank">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="currentColor"
                class="bi bi-github"
                viewBox="0 0 16 16"
              >
                <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
              </svg>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
